import { UserType } from '../HW8';

type ActionType =
    | { type: 'sort'; payload: 'up' | 'down' }
    | { type: 'check'; payload: number };

export const homeWorkReducer = (state: UserType[], action: ActionType): UserType[] => {
    switch (action.type) {
        case 'sort': {
            const newState = [...state]; // Create a copy of the state
            if (action.payload === "up") {
                newState.sort((a, b) => a.name.localeCompare(b.name));
            } else if (action.payload === "down") {
                newState.sort((a, b) => b.name.localeCompare(a.name)); // Reverse sorting
            }
            return newState;
        }
        case 'check': {
            // Filter and sort the state
            const newState = state.filter(u => u.age >= action.payload).sort((a, b) => a.name.localeCompare(b.name));
            return newState;
        }
        default:
            return state;
    }
};
