import React, { ChangeEvent, KeyboardEvent, useState } from 'react'
import Greeting from './Greeting'
import { UserType } from './HW3'

type GreetingContainerPropsType = {
    users: UserType[] // need to fix any
    addUserCallback: (name: string)=>void // need to fix any
}

export const pureAddUser = (name: string, setError: (error:string)=>void, setName: (name:string)=>void, addUserCallback: (name: string)=>void ) => {
    // если имя пустое - показать ошибку, иначе - добавить юзера и очистить инпут
    if(!name.trim()){
        setError("Ошибка! Введите имя!")
    }else{
        addUserCallback(name)
        setName("")
    }
}

export const pureOnBlur = (name: string, setError: (error:string)=>void) => {
    // если имя пустое - показать ошибку
    if(!name.trim()){
        setError("Ошибка! Введите имя!")
    }else{
        setError("")
    }
}

export const pureOnEnter = (e: any, addUser: (name: string) => void) => {
    if (e && e.key === 'Enter' && e.target && e.target.value) {
        addUser(e.target.value);
        return true; // Return true when 'Enter' key is pressed and value is available
    }
    return false; // Return false otherwise
}


// более простой и понятный для новичков
// function GreetingContainer(props: GreetingPropsType) {

// более современный и удобный для про :)
const GreetingContainer: React.FC<GreetingContainerPropsType> = ({
    users,
    addUserCallback,
}) => {
    // деструктуризация пропсов
    const [name, setName] = useState<any>('') // need to fix any
    const [error, setError] = useState<any>('') // need to fix any

    const setNameCallback = (e: React.ChangeEvent<HTMLInputElement>) => {
        setError(""); // Сброс ошибки
        setName(e.target.value); // Установка нового значения
    }


    const addUser = () => {
        pureAddUser(name, setError, setName, addUserCallback)
    }

    const onBlur = () => {
        pureOnBlur(name, setError)
    }

    const onEnter = (e: any) => {
        if (e.key === 'Enter' &&  e.target.value.trim() === ""){

            setError("Ошибка! Введите имя!")
        }else{
            setError("")
        }


        pureOnEnter(e, addUser)
    }

    const totalUsers = users.length // need to fix
    let lastUserName = "";

    if (users.length > 0) {
        lastUserName = users[users.length - 1].name;
    }
    return (
        <Greeting
            name={name}
            setNameCallback={setNameCallback}
            addUser={addUser}
            onBlur={onBlur}
            onEnter={onEnter}
            error={error}
            totalUsers={totalUsers}
            lastUserName={lastUserName}
        />
    )
}

export default GreetingContainer
