
// Типы состояния
type ThemeState = {
    themeId: number;
};

// Начальное состояние
const initState: ThemeState = {
    themeId: 1,
};

// Типы действий
const SET_THEME_ID = 'SET_THEME_ID';

// Редьюсер
export const themeReducer = (state: ThemeState = initState, action: ThemeAction): ThemeState => {

    switch (action.type) {
        case SET_THEME_ID:

            return {
                ...state,
                themeId: action.payload.id,
            };
        default:
            return state;
    }
};

// Типы действий
type ThemeAction = {
    type: typeof SET_THEME_ID;
    payload: {
        id: number;
    };
};

// Экшн-креатор
export const changeThemeId = (id: number): ThemeAction => ({
    type: SET_THEME_ID,
    payload: { id },
});
